import {Injectable} from '@angular/core';
/** Service */
import {SessionService} from '../session/session.service';


@Injectable({
  providedIn: 'root'
})
export class UserSessionService {

  constructor(private sessionService: SessionService) {
  }

  getUser(): any {
    return this.sessionService.getSession('auth_user');
  }

  putUser(user: string) {
    this.sessionService.putSession('auth_user', user);
  }

  deleteUser() {
    this.sessionService.deleteSession('auth_user');
  }
}
