import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'decreaseMinutes'
})
export class DecreaseMinutesPipe implements PipeTransform {

  transform(value: number, min: number): string {
    const val = (min * 60) - value;
    const minutes: number = Math.floor(val / 60);
    const seconds: number = val - minutes * 60;
    return (minutes < 10 ? '0' + minutes : minutes) + ':' + (seconds < 10 ? '0' + seconds : seconds);
  }

}
