import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SharedInterfaceService {

  public data: BehaviorSubject<any> = new BehaviorSubject<any>(null);
  public listData: BehaviorSubject<any>[] = [];
  public timeInactivity: BehaviorSubject<number> = new BehaviorSubject<number>(0);

  putData(data: any) {
    this.data.next(data);
  }

  putListData(data: any) {
    const item =  new BehaviorSubject<any>(null);
    item.next(data);
    this.listData.push(item);
  }

  putTimeInactivity(time: number) {
    this.timeInactivity.next(time);
  }
}
