import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DialogData } from '../../../interfaces/utils/dialog-data';
import { TokenService } from '../../../services/authentication/token/token.service';
import { UserIdleService } from 'angular-user-idle';
import { SharedInterfaceService } from '../../../services/utils/shared/shared-interface.service';
@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.scss']
})

export class DialogComponent implements OnInit {
  public time: number;

  constructor(
    private router: Router,
    private tokenService: TokenService,
    private userIdleService: UserIdleService,
    private sharedInterfaceService: SharedInterfaceService,
    public dialogRef: MatDialogRef<DialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) {
  }

  async ngOnInit() {
    if (this.data.type === 5)
      this.sharedInterfaceService.timeInactivity.subscribe((data) => this.time = data);
  }

  onClick() {
    this.dialogRef.close();
    this.router.navigate([this.data.url]);
  }

  onClose() {
    this.dialogRef.close();
  }

  onCloseAdmin() {
    this.data.message = 'accept';
    this.dialogRef.close();
  }

  /** Session Expired*/
  logout() {
    this.dialogRef.close();
    this.tokenService.deleteToken();
    this.router.navigate([this.data.url]);
  }

  singIn() {
    if (this.time < 120) {
      this.userIdleService.stopTimer();
      this.userIdleService.stopWatching();
      this.userIdleService.startWatching();
      this.userIdleService.resetTimer();
      this.dialogRef.close();
    }
  }

}
